import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

import Workout from '../../components/workout';
import WorkoutSummary from '../../components/workout/summary';
import DogHistory from '../../components/dog/history';
import LineGraph from '../../components/lineGraph';
import DogAddLog from '../../components/dog/addlog';

const MONTHS = ['jan', 'feb', 'mar', 'apr', 'mai', 'jun', 'jul', 'aug', 'sep', 'okt', 'nov', 'des'];

const initialState = {
	isLoading: false,
};

@observer
class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.loadAll();
    }

	async loadAll() {
		const { appState, userStore } = this.props.stores;
		this.setState({ isLoading: true });
        await appState.getFingerprint();
        await userStore.getInfo();
		await this.loadFeed();
		await this.loadWorkoutSummary();
		await this.loadDogs();
		this.setState({ isLoading: false });
    }

	async loadFeed() {
		const { userStore } = this.props.stores;
        await userStore.getFeed();
    }

	async loadWorkoutSummary() {
		const { userStore } = this.props.stores;
        await userStore.getWorkoutSummary();
    }

	loadDogs = async () => {
		this.setState({ isLoading: true });
		const { dogStore } = this.props.stores;
		const { currentTeam } = dogStore;

		await dogStore.load({ query: { team: currentTeam }, addData: ['workoutSummary'] });
		this.setState({ isLoading: false });
	}

	// componentWillUnmount() {
	//     PubSub.unsubscribe(subscription);
	// }

	render() {
		const { isLoading } = this.state;
		const { userStore } = this.props.stores;
		const {
			workouts, historyFeed, workoutSummary,
			graphWorkoutSummaryWeekDistanceCurrent, graphWorkoutSummaryWeekDistancePrev,
			graphWorkoutSummaryWeekElevationCurrent, graphWorkoutSummaryWeekElevationPrev,
			teams, dogs, tracks,
		} = userStore;
		const totalTeams = teams.length;
		const totalDogs = dogs.length;
		const totalTracks = tracks.length;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
				<div class='row'>
					<div class='col-12 mb-4'>
						{/* <h5 class='mt-4'>user</h5>
						{JSON.stringify(user)} */}
						{/* <h5 class='mt-4'>team</h5>
						{JSON.stringify(team)} */}
					</div>
				</div>
				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>

						{!isLoading && workoutSummary && workoutSummary.length === 0 && (
							<div class='mb-5 py-3 border-top border-bottom'>
								<div class='text-center'>
									<div class='display-4'>
										<i class='fas fa-paw' />
									</div>
									<h5>
										Velkommen til The Musher.
									</h5>
									<p>
										The Musher er en app som er kontinuerlig under utvikling.
										Alle ideer/innspill/kommentarer mottas med stor takk på Facebooksiden vår 😃
									</p>
									<p>
										Fokus til appen er enkel registrering av hunder, team, treninger og hendelser rundt hundene.
										Når man har mange hunder så er det ofte vanskelig å holde styr på løpetid, vaksiner, treninger,
										skader, vetrinærbesøk, chiplister, osv.
									</p>
									<p>
										Alle data lagres sentralt i skyen hos AWS (Amazon Web Services) og det vil derfor være mulig å bruke appen fra flere telefoner
										og datamaskiner samtidig. Dette kan være fint dersom det er flere som trener og følger opp hundene.
									</p>
								</div>

								<h5>
									Slik kommer du i gang:
								</h5>
								<ol>
									{totalTeams < 1 && <li><a class='btn btn-info mt-3' href='/teams/'>Legg inn teamene dine.</a></li>}
									{totalDogs < 1 && <li><a class='btn btn-info mt-3' href='/dogs/'>Legg inn hundene dine.</a></li>}
									{totalTracks < 1 && <li><a class='btn btn-info mt-3' href='/tracks/'>Legg inn rundene dine.</a></li>}
									<li><a class='btn btn-info mt-3' href='/workouts/'>Begynn å registrere treningene.</a></li>
								</ol>
							</div>
						)}

						{workoutSummary && workoutSummary.length > 0 && (
							<>
								<h5>Trenings statistikk</h5>
								<div class='w-100 mt-3 mb-4'>
									<small class='mb-4'><small>
										<table class='table table-sm font-weigth-lighter border-bottom'>
											<thead>
												<tr>
													<th style='padding: .2rem;'>Mnd</th>
													{util.range(1, 12).map(w => (
														<th class='text-right' style='padding: .2rem;'>{MONTHS[w - 1]}</th>
													))}
												</tr>
											</thead>
											<tbody>
												<tr>
													<th scope='row' style='padding: .2rem;'>2019</th>
													{util.range(1, 12).map(m => (
														<>
															<td class='text-right' style='padding: .2rem;'>
																{userStore.findWorkoutSummaryMonth(2019, m)}<br />
																{userStore.findWorkoutSummaryMonth(2019, m, 'elevation')}<br />
															</td>
														</>
													))}
													<td class='text-muted' style='padding: .2rem;'>
														<nobr><i class='fas fa-road' /> km</nobr><br />
														<nobr><i class='fas fa-mountain' /> m</nobr><br />
													</td>
												</tr>
												<tr>
													<th scope='row' style='padding: .2rem;'>2020</th>
													{util.range(1, 12).map(m => (
														<>
															<td class='text-right' style='padding: .2rem;'>
																{userStore.findWorkoutSummaryMonth(2020, m)}<br />
																{userStore.findWorkoutSummaryMonth(2020, m, 'elevation')}<br />
															</td>
														</>
													))}
													<td class='text-muted' style='padding: .2rem;'>
														<nobr><i class='fas fa-road' /> km</nobr><br />
														<nobr><i class='fas fa-mountain' /> m</nobr><br />
													</td>
												</tr>
											</tbody>
										</table>
									</small></small>
												
									<LineGraph
										stores={this.props.stores}
										width={500}
										height={150}
										showXTicks={1}
										xTicksVal={val => `uke ${val}`}
										paddingTop={30}
										lineFromXzero={true}
							            lineFromXmax={true}

										dataLeft={[graphWorkoutSummaryWeekDistanceCurrent, graphWorkoutSummaryWeekDistancePrev]}
										legendLeft={['2020 Distanse', '2019 Distanse']}
										// yMaxLeft={30}
										// yMinLeft={0}
										showYTicksLeft={1}
										yTicksLeftPostfix={'km'}
										unitLeft={['km', 'km']}
										avgWindowLeft={[0, 0]}
										smoothLeft={[1, 1]}

										dataRight={[graphWorkoutSummaryWeekElevationCurrent, graphWorkoutSummaryWeekElevationPrev]}
										legendRight={['2020 Høydemeter', '2019 Høydemeter']}
										showYTicksRight={1}
										yTicksRightPostfix={'m'}
										unitRight={['m', 'm']}
										avgWindowRight={[0, 0]}
										smoothRight={[1, 1]}

										// marker={week}
									/>
								</div>
							</>
						)}

						{!isLoading && workoutSummary && workoutSummary.length < 3 && (
							<div class='mb-5 py-3 border-top border-bottom'>
								<div class='text-left' style='line-height: 1.0em;'>
									<div class='display-4 float-left text-warning mr-3'>
										<i class='fas fa-paw' />
									</div>
									<small>
										Foreløpig er det litt lite data for å kunne vise noen fine grafer. 
										Etterhvert som du legger inn flere treninger så vil dette bli en fin tabell og graf som viser progresjonen din.
									</small>
								</div>
							</div>
						)}
					</div>
				</div>
				<div class='row'>
					<div class='col-12 mb-4'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
							<span class='float-right'><DogAddLog stores={this.props.stores} dog={{}} /></span>
							<h5>Siste fra hundene</h5>
						</div>

						{!isLoading && historyFeed && historyFeed.length === 0 && (
							<div class='mb-5 py-3 text-center border-top border-bottom text-muted'>
								<div class='display-4'>
									<i class='fas fa-dog' />
								</div>
								<h5>
									Ingen hendelser fra hundene.
								</h5>
								Det er fint å legge inn hendelser (løpetid, skader, vaksinering, vetrinærbesøk, mm) og bilder av hundene i loggen.<br />
								<br />
								På denne måten så vil hele teamet ditt se hva som skjer med hundene og hva som trengs å følges opp.
								Dette er en slags <strike>Face</strike>Dogbook for hundene deres.
								<br />
								<a class='btn btn-info mt-3' href='/dogs/'>
									<i class='fas fa-dog' /> Legg til logg på hundene
								</a>
							</div>
						)}

						{historyFeed && historyFeed.map(line => (
							<DogHistory stores={this.props.stores} line={line} />
						))}
					</div>
				</div>
				{/* <div class='row'>
					<div class='col-12 mb-4'>
						<h5>Hundene - {dogs.length} stk</h5>
						<div class='row'>
							{dogs && dogs.map(dog => (
								<Dog stores={this.props.stores} dog={dog} />
							))}
						</div>
					</div>
				</div> */}
				<div class='row'>
					{workouts && workouts.length > 0 && <div class='col-12 mb-4'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>		
							<h5>Siste treningsturer - {workouts.length} stk</h5>
						</div>
						{workouts && workouts.map(workout => (
							<Workout stores={this.props.stores} workout={workout} />
						))}
					</div>}
				</div>
				<div class='row'>
					{workoutSummary && workoutSummary.length > 0 && <div class='col-12 mb-4'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
							<h5 class='mt-4'>Treningsoversikt</h5>
						</div>
						{/* {JSON.stringify(workoutSummary)} */}
						{workoutSummary && workoutSummary.map(summary => (
							<WorkoutSummary stores={this.props.stores} summary={summary} />
						))}
					</div>}
				</div>
			</div>
		);
	}
}

export default Home;
